import PXSceneRenderer from '../../renderers/pxscene/PXSceneRenderer';
import { SHAPES } from '../../const';
import { hex2rgb, rgb2rgba, rgba2hex } from '../../utils';

// Map: font family name -> fontUrl
// Set key lowercase
const CIRCLE_IMAGE_PATH = 'https://pxscene-pixi-js-tc-bill.herokuapp.com/assets/circle-mask.png';

/**
 * Renderer dedicated to drawing and batching graphics objects.
 *
 * @class
 * @private
 * @memberof PIXI
 */
export default class PXSceneGraphicsRenderer
{
    /**
     * @param {PIXI.CanvasRenderer} renderer - The current PIXI renderer.
     */
    constructor(renderer)
    {
        this.renderer = renderer;

        this.prevPoint = null;

        this.renderOptions = null;

        this.graphics = null;

        this.circleResource = null;
    }

    /**
     * Moves the current drawing position to x, y.
     *
     * @param {number} x - the X coordinate to move to
     * @param {number} y - the Y coordinate to move to
     */
    moveTo(x, y)
    {
        this.prevPoint = { x, y };
    }

    /**
     * Draws a line using the current line style from the current drawing position to (x, y);
     * The current drawing position is then set to (x, y).
     *
     * @param {number} x - the X coordinate to draw to
     * @param {number} y - the Y coordinate to draw to
     */
    lineTo(x, y)
    {
        const curPoint = { x, y };
        const graphics = this.graphics;

        graphics.renderedObjects.push(this.drawLine(this.prevPoint, curPoint, this.renderOptions));
        this.prevPoint = curPoint;
    }

    /**
     * Renders a Graphics object to a canvas.
     *
     * @param {PIXI.Graphics} graphics - the actual graphics object to render
     */
    render(graphics)
    {
        if (!graphics || !graphics.renderedObject)
        {
            return;
        }

        this.graphics = graphics;
        graphics.clearAllRenderedObjects();

        const worldAlpha = graphics.worldAlpha;

        // if the tint has changed, set the graphics object to dirty.
        if (this._prevTint !== this.tint)
        {
            this.dirty = true;
        }

        if (graphics.dirty)
        {
            this.updateGraphicsTint(graphics);
            graphics.dirty = false;
        }

        for (let i = 0; i < graphics.graphicsData.length; i++)
        {
            const data = graphics.graphicsData[i];
            const shape = data.shape;

            const fillColor = rgba2hex(rgb2rgba(hex2rgb(data._fillTint)));
            const strokeLineColor = rgba2hex(rgb2rgba(hex2rgb(data._lineTint)));

            const strokeLineWidth = data.lineWidth;

            const options = {
                globalAlpha: data.fillAlpha * worldAlpha,
                fillColor,
                strokeLineColor,
                strokeLineWidth,
            };

            if (data.type === SHAPES.POLY)
            {
                // Not support fill.

                this.renderOptions = options;
                this.renderPolygon(shape.points);

                for (let j = 0; j < data.holes.length; j++)
                {
                    this.renderPolygon(data.holes[j].points);
                }
                this.renderOptions = null;
            }
            else if (data.type === SHAPES.RECT)
            {
                // Draw rect directly
                this.renderOptions = options;

                this.renderRect(shape);

                this.renderOptions = null;
            }
            else if (data.type === SHAPES.CIRC)
            {
                // A trick way, use an image as a mask
                // Not support outlines.

                this.renderOptions = options;

                if (!this.circleResource)
                {
                    const circleResource = this.renderer.view.create({
                        t: 'imageResource',
                        url: CIRCLE_IMAGE_PATH,
                    });

                    circleResource.ready.then((resource) =>
                    {
                        this.circleResource = resource;
                        this.renderCircle(shape);
                    });
                }
                else
                {
                    this.renderCircle(shape);
                }

                this.renderOptions = null;
            }
            else if (data.type === SHAPES.ELIP)
            {
                // TODO
                // Not supported yet
            }
            else if (data.type === SHAPES.RREC)
            {
                // TODO
                // Not supported yet
            }
        }

        this.graphics = null;
    }

    /**
     * Updates the tint of a graphics object
     *
     * @private
     * @param {PIXI.Graphics} graphics - the graphics that will have its tint updated
     */
    updateGraphicsTint(graphics)
    {
        graphics._prevTint = graphics.tint;

        const tintR = ((graphics.tint >> 16) & 0xFF) / 255;
        const tintG = ((graphics.tint >> 8) & 0xFF) / 255;
        const tintB = (graphics.tint & 0xFF) / 255;

        for (let i = 0; i < graphics.graphicsData.length; ++i)
        {
            const data = graphics.graphicsData[i];

            const fillColor = data.fillColor | 0;
            const lineColor = data.lineColor | 0;

            // super inline cos im an optimization NAZI :)
            data._fillTint = (
                (((fillColor >> 16) & 0xFF) / 255 * tintR * 255 << 16)
                + (((fillColor >> 8) & 0xFF) / 255 * tintG * 255 << 8)
                + (((fillColor & 0xFF) / 255) * tintB * 255)
            );

            data._lineTint = (
                (((lineColor >> 16) & 0xFF) / 255 * tintR * 255 << 16)
                + (((lineColor >> 8) & 0xFF) / 255 * tintG * 255 << 8)
                + (((lineColor & 0xFF) / 255) * tintB * 255)
            );
        }
    }

    /**
     * Renders a polygon.
     *
     * @param {PIXI.Point[]} points - The points to render
     * @param {boolean} close - Should the polygon be closed
     * @param {CanvasRenderingContext2D} context - The rendering context to use
     */
    renderPolygon(points)
    {
        this.moveTo(points[0], points[1]);

        for (let j = 1; j < points.length / 2; ++j)
        {
            this.lineTo(points[j * 2], points[(j * 2) + 1]);
        }
    }

    /**
     * Renders a rectangle.
     *
     * @param {Object} shape - The shape data.
     */
    renderRect(shape)
    {
        const graphics = this.graphics;

        if (!graphics || !graphics.renderedObject)
        {
            return;
        }
        const parentRenderedObject = graphics.renderedObject || this.renderer.context;

        const rect = this.renderer.view.create({
            t: 'rect',
            parent: parentRenderedObject,
        });

        const renderOptions = this.renderOptions;

        rect.x = shape.x;
        rect.y = shape.y;
        rect.w = shape.width;
        rect.h = shape.height;
        rect.a = renderOptions.globalAlpha;
        rect.fillColor = renderOptions.fillColor;
        rect.lineColor = renderOptions.strokeLineColor;
        rect.lineWidth = renderOptions.strokeLineWidth;

        graphics.renderedObjects.push(rect);
    }

    /**
     * Renders a circle.
     *
     * @param {Object} shape - The shape data.
     */
    renderCircle(shape)
    {
        const graphics = this.graphics;

        if (!graphics || !graphics.renderedObject)
        {
            return;
        }
        const parentRenderedObject = graphics.renderedObject || this.renderer.context;

        const renderOptions = this.renderOptions;
        const spriteWidth = this.circleResource.w;
        const spriteHeight = this.circleResource.h;

        const circleRadius = spriteWidth / 2;

        const ratio = shape.radius / circleRadius;

        const circle = this.renderer.view.create({
            t: 'rect',
            parent: parentRenderedObject,
            clip: true,
            fillColor: 0x0,
        });

        circle.w = spriteWidth * ratio;
        circle.h = spriteHeight * ratio;
        circle.cx = circle.w * 0.5;
        circle.cy = circle.w * 0.5;
        circle.x = shape.x - circle.cx;
        circle.y = shape.y - circle.cy;

        const circleMaskImage = this.renderer.view.create({
            t: 'image',
            resource: this.circleResource,
            parent: circle,
            mask: true,
            draw: false,
        });

        let cx = spriteWidth * 0.5;
        let cy = spriteHeight * 0.5;

        circleMaskImage.x = -cx + circle.cx;
        circleMaskImage.y = -cy + circle.cy;
        circleMaskImage.cx = cx;
        circleMaskImage.cy = cy;
        circleMaskImage.sx = ratio;
        circleMaskImage.sy = ratio;

        const circleView = this.renderer.view.create({
            t: 'rect',
            parent: circle,
        });

        cx *= ratio;
        cy *= ratio;

        circleView.x = 0;
        circleView.y = 0;
        circleView.w = circleRadius * 2 * ratio;
        circleView.h = circleRadius * 2 * ratio;
        circleView.cx = cx;
        circleView.cy = cy;
        circleView.fillColor = renderOptions.fillColor;
        circleView.a = renderOptions.globalAlpha;

        graphics.renderedObjects.push(circle);
    }

    /**
     * Draw a single line
     *
     * @param {Object} startPoint - Start point of the line.
     * @param {Object} endPoint - End point of the line
     * @param {Object} options - extra options of the line
     * @return {Object} The line, Render Target object in pxscene.
     */
    drawLine(startPoint, endPoint, options)
    {
        const graphics = this.graphics;

        if (!graphics || !graphics.renderedObject)
        {
            return null;
        }
        const parentRenderedObject = graphics.renderedObject || this.renderer.context;

        let lineWidth = 2;
        let color = 0xFFFFFFFF;
        let alpha = 1;

        if (options)
        {
            lineWidth = options.strokeLineWidth;
            color = options.strokeLineColor;
            alpha = options.globalAlpha;
        }

        const line = this.renderer.view.create({
            t: 'rect',
            parent: parentRenderedObject,
        });

        line.x = startPoint.x;
        line.y = startPoint.y;

        const xDiff = endPoint.x - startPoint.x;
        const yDiff = endPoint.y - startPoint.y;

        const width = Math.sqrt((xDiff * xDiff) + (yDiff * yDiff));

        const rotation = Math.atan2(yDiff, xDiff);

        const height = lineWidth;

        line.w = width;
        line.h = height;
        line.fillColor = color;
        line.r = rotation * 180 / Math.PI;
        line.a = alpha;

        return line;
    }

    /**
     * destroy graphics object
     *
     */
    destroy()
    {
        this.renderer = null;
        this.circleResource = null;
        this.graphics = null;
    }
}

PXSceneRenderer.registerPlugin('graphicsV8', PXSceneGraphicsRenderer);
